import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import en from "./en";
import ko from "./ko";

const resources = {
    en: { translation: en },
    ko: { translation: ko }
} 

// i18n 
i18n.use(initReactI18next).init({
    resources,
    lng: 'en', //   
    fallbackLng: 'en',
    interpolation: {
        escapeValue: false
    }
});

export default i18n;